	module.exports = {"data":{"markdownRemark":{"html":"<h2 id=\"custom-arrows\"><a href=\"#custom-arrows\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Custom arrows</h2>\n<p>You can set custom arrows using arrowLeft and arrowRight props. You can handle arrow click event by yourself, or you can tell Carousel to do that for you (using addArrowClickHandler prop).</p>\n\n  <div>\n    <div class=\"gatsby-render-code\" ></div>\n    <div class=\"gatsby-render-source-code\" style=\"display:none;\">\n    // import Icon from &#39;react-fa&#39;;\n&lt;Carousel\n    arrowLeft={&lt;Icon name=&quot;angle-double-left&quot; /&gt;}\n    arrowRight={&lt;Icon name=&quot;angle-double-right&quot; /&gt;}\n    addArrowClickHandler\n&gt;\n  &lt;img src={imageOne} /&gt;\n  &lt;img src={imageTwo} /&gt;\n  &lt;img src={imageThree} /&gt;\n&lt;/Carousel&gt;\n    </div>\n  </div>"}},"pathContext":{"relativePath":"GDK>../docs/examples/customArrows.md"}}

